import Header from '../../components/Header';
import { useParams } from 'react-router-dom';
import { useState, useEffect } from 'react';
import axios from 'axios';
import axiosRetry from 'axios-retry';
import { Link } from 'react-router-dom';
function MovieDetail() {
    const { slug } = useParams();
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    axiosRetry(axios, {
        retries: 3, // S ln th li
        retryDelay: (retryCount) => {
            return retryCount * 2000; // Thi gian ch gia cc ln th li (2s, 4s, 6s, ...)
        },
        retryCondition: (error) => {
            // Th li ch khi gp li 429
            return error.response.status === 429;
        },
    });
    useEffect(() => {
        axios
            .get(` https://phim.nguonc.com/api/film/${slug}`)
            .then((response) => {
                setData(response.data);
                setLoading(false);
            })
            .catch((error) => {
                setError(error);
                setLoading(false);
            });
    }, []);
    if (loading) {
        return <div>Loading...</div>;
    }

    if (error) {
        return <div>Error: {error.message}</div>;
    }
    console.log(data.movie.episodes[0].items);
    return (
        <div className="bg-secondary">
            <Header />
            <div className="container mt-5 bg-dark">
                <div className="row">
                    <div
                        style={{
                            height: '500px',
                            backgroundImage: `url(${data.movie.poster_url})`,
                        }}
                        className=" background col-lg-12"
                    ></div>
                </div>
                <div className="row mt-5">
                    <div className="col-lg-12">
                        <span className="title">{data.movie.name}</span>
                    </div>
                    <div className="col-lg-12">
                        <span style={{ color: 'white' }}>{data.movie.original_name}</span>
                    </div>
                </div>
                <div className="row mt-5">
                    <div className="col-lg-12">
                        <p className="test">Tp phim :</p>
                        {data.movie.episodes[0].items.map((item, index) => (
                            <Link key={index} to={item.embed}>
                                <button>{item.name}</button>
                            </Link>
                        ))}
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-6">
                        <div className="row mt-3 ">
                            <div className="col-lg-4 d-flex justify-content-end">
                                <p className="test">Tng tp :</p>
                            </div>
                            <div className="col-lg-8">
                                <p className="test">{data.movie.total_episodes}</p>
                            </div>
                        </div>{' '}
                        <div className="row ">
                            <div className="col-lg-4 d-flex justify-content-end">
                                <p className="test">Thi lng :</p>
                            </div>
                            <div className="col-lg-8">
                                <p className="test">{data.movie.time}</p>
                            </div>
                        </div>{' '}
                        <div className="row  ">
                            <div className="col-lg-4 d-flex justify-content-end">
                                <p className="test">Cht lng :</p>
                            </div>
                            <div className="col-lg-8 d-flex">
                                <p className="test">{data.movie.quality}</p>
                            </div>
                        </div>
                        <div className="row  ">
                            <div className="col-lg-4 d-flex justify-content-end">
                                <p className="test">Quc gia :</p>
                            </div>
                            <div className="col-lg-8 d-flex">
                                <p className="test">{data.movie.category[4].list[0].name}</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6">
                        <div className="row mt-3 ">
                            <div className="col-lg-4 d-flex justify-content-end">
                                <p className="test">o din :</p>
                            </div>
                            <div className="col-lg-8">
                                <p className="test">{data.movie.director}</p>
                            </div>
                        </div>{' '}
                        <div className="row ">
                            <div className="col-lg-4 d-flex justify-content-end">
                                <p className="test">Din vin :</p>
                            </div>
                            <div className="col-lg-8">
                                <p className="test">{data.movie.casts}</p>
                            </div>
                        </div>{' '}
                        <div className="row  ">
                            <div className="col-lg-4 d-flex justify-content-end">
                                <p className="test">Nm :</p>
                            </div>
                            <div className="col-lg-8 d-flex">
                                <p className="test">{data.movie.category[3].list[0].name}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-12">
                        <p className="test">Ni dung phim :</p>
                        <p className="test">{data.movie.description}</p>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default MovieDetail;
