import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { BrowserRouter as Router, Route, Switch, Link } from 'react-router-dom';

function Header() {
    return (
        <Navbar expand="lg" className="bg-dark navbar-dark">
            <Container>
                <Navbar.Brand href="/">Movie</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link as={Link} to="/">
                            Trang ch
                        </Nav.Link>
                        <Nav.Link as={Link} to="/phimle">
                            Phim l
                        </Nav.Link>
                        <Nav.Link href="#link">Phim b</Nav.Link>
                        <NavDropdown title="Quc gia" id="basic-nav-dropdown">
                            <NavDropdown.Item href="#action/3.1">Vit Nam</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.2">Hn Quc</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.3">Thi Lan</NavDropdown.Item>
                        </NavDropdown>
                        <Form className="d-flex">
                            <Form.Control
                                type="search"
                                placeholder="Tm kim"
                                className="me-2 bg-dark border border-secondary  "
                                aria-label="Search"
                            />
                            <Button variant="outline-success">Search</Button>
                        </Form>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

export default Header;
